<template>
  <div id="app">
    <v-header @search="search" @back="back" class="header"></v-header>
    <v-search @playsearch="playbysrc" ref="search"></v-search>
    <songslist @playsonglistobjstr="playsong" ref="songslist"></songslist>
    <song @startLyric="startLyric" @setTimeindex="setTimeindex" @isshow="setsearch" ref="song"></song>
    <keep-alive>
      <router-view @playsonglistobjstr="playsong" @songlistshow="showSongList" @songlistobjstr="setSongList"></router-view>
    </keep-alive>
    <div class="player-wrapper">
      <v-player @startLyric="startLyric" @setTimeindex="setTimeindex" @last="last" @next="next" @showPlaysong="showPlaysong" ref="player" class="player"></v-player>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import header from "./components/header/header"
import player from "./components/player/player"
import songslist from "./components/songslist/songslist"
import song from "./components/song/song"
import search from "./components/search/search"
export default {
 // name: 'app',
  data () {
    return {
      playsonglist: {},
      index: 0
    }
  },
  methods: {
    setSongList (songlistobjstr) {
      this.$refs.songslist.setData(songlistobjstr)
    },
    setsearch () {
      if (this.$refs.search.isshow === true) {
        if(this.$refs.song.show === true) {
          this.$refs.song.setShowNow()
        }
        this.$refs.search.setShow()
      }
    },
    showSongList () {
      this.$refs.songslist.setShow()
    },
    showPlaysong () {
      if (this.$refs.song.show === false) {
        this.$refs.song.setShow()
      } else {
        this.back()
      }
    },
    back () {
      if(this.$refs.song.show === true) {
        this.$refs.song.setShow()
        if (this.$refs.song.showlyric === true) {
          this.$refs.song.change()
        }
      } else if (this.$refs.songslist.show === true) {
        this.$refs.songslist.setShow()
      }
    },
    search () {
      this.$refs.search.setShow()
    },
    playbysrc (src) {
      let vm = this
      let jsonp = require('jsonp')
      jsonp(src, null, function (err, data) {
        if (err) {
          console.log(err)
        } else {
          let song = {}
          song.name = data.songinfo.title
          song.author = data.songinfo.author
          song.lyricsrc = data.songinfo.lrclink
          song.img = data.songinfo.pic_big
          song.songsrc = data.bitrate.file_link
          // let audio = new Audio()
          // audio.src = data.bitrate.file_link
          vm.$refs.song.playsong(song)
          vm.$refs.player.setData(song)
          vm.$refs.player.play()
        }
      })
    },
    play () {
      let songid = this.playsonglist[this.index].songid||this.playsonglist[this.index].song_id
      let jsonp = require('jsonp')
      let vm = this
      jsonp("http://tingapi.ting.baidu.com/v1/restserver/ting?from=qianqian&version=2.1.0&method=baidu.ting.song.play&songid="+songid, null, function (err, data) {
        if (err) {
          console.log(err)
        } else {
          let song = {}
          song.name = data.songinfo.title
          song.author = data.songinfo.author
          song.lyricsrc = data.songinfo.lrclink
          song.img = data.songinfo.pic_big
          song.songsrc = data.bitrate.file_link
          window.sessionStorage.setItem('songsrc',data.bitrate.file_link)
          vm.$refs.song.playsong(song)
          vm.$refs.player.setData(song)
          vm.$refs.player.play()
        }
      })
    },
    playsong (playsonglistobjstr) {
      let playsonglistobj = JSON.parse(playsonglistobjstr)
      let song = {
        name: "",
        author: "",
        lyricsrc: "",
        img: "",
        songsrc: ""
      }
      if (playsonglistobj.songlist.length||playsonglistobj.songlist.song_list) {
        this.playsonglist = playsonglistobj.songlist.song_list||playsonglistobj.songlist
        this.index = playsonglistobj.index
        this.play()
      } else {
        this.$refs.song.playsong(song)
      }
    },
    last () {
      if (!this.playsonglist.length) {
        window.alert("播放列表为空")
      } else {
        if(this.index === 0) {
          this.index = this.playsonglist.length - 1
        } else {
          this.index--
        }
        this.play()
      }

    },
    next () {
      if (!this.playsonglist.length) {
        window.alert("播放列表为空")
      } else {
        if(this.index === this.playsonglist.length - 1) {
          this.index = 0
        } else {
          this.index++
        }
        this.play()
      }
    },
    setTimeindex () {
      this.$refs.song.setTimeindex(this.$refs.player.audio.currentTime)
    },
    startLyric () {
      if(this.$refs.song.showlyric === true && this.$refs.player.isPlay === true) {
        this.$refs.song.startTimer()
      } else {
        this.$refs.song.stopTimer()
      }
    }
  },
  components: {
    'v-header': header,
    'v-player': player,
    'v-search': search,
    'songslist': songslist,
    'song': song
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
#app
  .player-wrapper
    position: absolute
    left: 0
    right: 0
    bottom: 0
    border-top: 1px solid rgb(50, 10, 10)
    background-color: #fff
  .player
    margin: 0 auto
    margin-top: -1.5px
    background-color: #fff
</style>



