<template>
  <transition name="move">
    <div ref="songwrapper" class="playsong" v-show="show" @click="change">
      <transition name="fade">
        <div class="img-minwrapper" v-show="!showlyric" key="img">
          <div class="img-maxwrapper">
            <img :src="img" alt="">
            <p class="name">{{name}}</p>
            <p class="author">{{author}}</p>
          </div>
        </div>
      </transition>
      <transition name="fade">
        <div ref="lyricminwrapper" class="lyric-minwrapper" v-show="showlyric" key="lyric">
          <div ref="lyricWrapper" class="lyric-maxwrapper">
            <p ref="lyric" v-for="(lyric, index) of lyrics" v-show="lyric!='\n'&&lyric!=''" class="lyric" :class='onlyric===index?"onlyric":""'>{{lyric}}</p>
          </div>
        </div>
      </transition>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import axios from 'axios'
  import {getlyric} from '../../common/js/myfunction'
  export default {
    data () {
      return {
        img: require('./123.jpg'),
        name: "第三人称",
        author: "达布",
        lyricsrc: "",
        lyrictime: [],
        lyrics: [],
        show: false,
        showlyric: false,
        onlyric: 0,
        timeindex: 0,
        songindex: 0,
        time: {},
        top: 0,
      }
    },
    methods: {
      setShowBefore () {
        this.$emit('isshow')
      },
      setShowNow () {
        this.show = !this.show
      },
      setShow () {
        this.setShowBefore()
        this.setShowNow()
      },
      setTimeindex (timeindex) {
        this.timeindex = timeindex
      },
      change () {
        this.showlyric = !this.showlyric
      },
      playsong (song) {
        this.img = song.img
        this.name = song.name
        this.author = song.author
        if (song.lyricsrc != "") {
          this.lyricsrc = song.lyricsrc
          this.setlyric(this.lyricsrc)
        }
      },
      setlyric (lyricsrc) {
        axios.get(lyricsrc).then((res) => {
          let obj = getlyric(res.data)
          this.lyrics = obj.lyric
          this.lyrictime = obj.time
        }).catch((err) => {
          console.log(err)
        })

      },
      changeOnlyric () {
        for(let i = 0; i < this.lyrics.length - 1; i++) {
          if (this.timeindex >= this.lyrictime[this.lyrics.length - 1]) {
            this.onlyric = this.lyrics.length - 2
            return
          } else if (this.timeindex <= this.lyrictime[0]) {
            this.onlyric = 0
            return
          } else if (this.timeindex >= this.lyrictime[i] && this.timeindex < this.lyrictime[i + 1]) {
            this.onlyric = i + 1
            return
          }
        }
      },
      startTimer () {
        clearInterval(this.time)
        this.time = setInterval(() => {
          this.timeindex += 0.3
        }, 300)
      },
      stopTimer () {
        clearInterval(this.time)
      },
      moveLyric () {
        if (this.$refs.lyricminwrapper.style.display === "none") {
          this.$refs.lyricminwrapper.style.display = "block"
        }
        let x = this.$refs.songwrapper.offsetHeight / 2 - this.$refs.lyric[this.onlyric].offsetTop
        this.$refs.lyricWrapper.style.top = x + "px"
        if (this.showlyric === false) {
          this.$refs.lyricminwrapper.style.display = "none"
        }
      }
    },
    created () {

    },
    watch: {
      lyrics () {
        this.timeindex = 0
        this.$refs.lyricWrapper.style.top = this.$refs.songwrapper.offsetHeight / 2 + "px"
        if (this.showlyric === true) {
          this.startTimer()
        }
      },
      showlyric () {
        this.moveLyric()
        this.$emit("setTimeindex")
        this.$emit("startLyric")
      },
      timeindex () {
        this.changeOnlyric()
      },
      onlyric () {
        if (this.lyrics[this.onlyric] === "\n" || this.lyrics[this.onlyric] === "") {
          if (this.onlyric != this.lyrics.length - 1){
            this.onlyric++
          } else {
            this.onlyric--
          }
        }
        this.moveLyric()
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
.playsong
  position: fixed
  display: flex
  left: 0
  width: 100%
  top: 45px
  bottom: 64px
  z-index: 160
  background: #fff
  align-items: center
  font-size: 0
  transition: all 0.15s linear
  &.move-enter, &.move-leave-to
    transform: translate(0, 100%)
  .img-minwrapper
    position: absolute
    top: 0
    width: 100%
    height: 100%
    box-sizing: border-box
    overflow-y: scroll
    transition: all 0.1s linear
    &.fade-enter, &.fade-leave-to
      opacity: 0
    .img-maxwrapper
      position: absolute
      top: 0
      border-top: 30px solid rgba(50, 10, 10, 0)
      width: 100%
      text-align: center
      img
        width: 83%
        max-width: 320px
      .name
        font-size: 20px
        color: rgb(50, 10, 10)
        margin-top: 25px
      .author
        font-size: 18px
        color: rgb(50, 10, 10)
        margin-top: 15px
        margin-bottom: 10px
  .lyric-minwrapper
    width: 100%
    height: 100%
    border-top: 20px solid rgba(240, 240, 240, 0)
    border-bottom: 20px solid rgba(240, 240, 240, 0)
    box-sizing: border-box
    transition: all 0.3s linear
    &.fade-enter, &.fade-leave-to
      opacity: 0
    .lyric-maxwrapper
      position: absolute
      top: 50%
      width: 100%
      text-align: center
      transition: all 0.1s linear
      .lyric
        height: 28px
        font-size: 15px
        line-height: 28px
        color: rgba(50, 10, 10, 0.6)
        &.onlyric
          color: rgb(50, 10, 10)
</style>



