<template>
  <transition name="move">
    <div class="searchmain" v-show="isshow">
      <div class="searchheader">
        <span class="exit" @click="setShow"><i class="fa fa-angle-double-left"></i></span>
        <span class="search-wrapper">
          <input type="edit" name="" class="searchview" v-model="text">
          <span class="enter" @click="searchsong()">确认</span>
        </span>
      </div>
      <ul class="list-wrapper">
        <li v-for="(search, index) of searchlist" class="searchlist" @click="detail(search.songid)">
          <div class="searchindex-wrapper">
            <p class="songindex">{{index+1}}、</p>
          </div>
          <div class="searchtext-wrapper">
            <p class="songname">{{search.songname}}</p>
            <p class="songauthor">{{search.artistname}}</p>
          </div>
        </li>
      </ul>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  export default {
    data(){
      return{
        searchlist: [],
        text: '',
        isshow: false
      }
    },
    methods: {
      setShow () {
        this.isshow = !this.isshow
      },
      searchsong () {
        let jsonp = require('jsonp')
        let vm = this
        jsonp("http://tingapi.ting.baidu.com/v1/restserver/ting?from=qianqian&version=2.1.0&method=baidu.ting.search.catalogSug&format=json&query="+this.text, null, function(err, data){
          if (err) {
            console.log(err)
          } else {
            vm.searchlist = data.song
          }
        })
      },
      detail: function (songid) {
        let song = "http://tingapi.ting.baidu.com/v1/restserver/ting?from=qianqian&version=2.1.0&method=baidu.ting.song.play&songid="+songid
        this.$emit('playsearch', song)
      }
    },
    watch: {
      text () {
        this.searchsong()
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
.searchmain
  position: fixed
  top: 0
  left: 0
  bottom: 64px
  width: 100%
  z-index: 200
  background-color: rgb(240,240,240)
  transition: all 0.1s linear
  &.move-enter, &.move-leave-to
    transform: translate(100%, 0)
  .searchheader
    display: flex
    width: 100%
    height: 45px
    background-color: rgb(50,10,10)
    font-size: 0
    .exit
      text-align: center
      font-size: 15px
      font-weight: 100
      color: rgb(230,230,230)
      width: 12%
      height: 45px
      line-height: 45px
    .search-wrapper
      display: flex
      width: 80%
      height: 40px
      border-bottom: 1px solid rgb(230,230,230)
      .enter
        width: 15%
        height: 40px
        padding-top: 15px
        text-align: center
        font-size: 15px
        color: rgb(230,230,230)
      .searchview
        width: 85%
        height: 20px
        line-height: 15px
        margin-top: 14px
        border: none
        background: none
        padding: 0
        outline: none
        color: rgb(250,250,250)
        font-weight: 100
        font-size: 15px
  .list-wrapper
    width: 100%
    height: 100%
    overflow-y: scroll
    .searchlist
      color: black
      height: 53px
      margin-top: 2px
      padding-left: 2px
      background-color: rgb(250,250,250)
      list-style: none
      overflow: hidden
      display: flex
      align-items: center
      font-size: 0
      .searchindex-wrapper
        height: 100%
        width: 50px
        float: left
        vertical-align: middle
        .songindex
          font-style: italic
          font-size: 15px
          width: 100%
          height: 100%
          text-align: center
          line-height: 53px
          align-items: center
      .searchtext-wrapper
        float: left
        vertical-align: middle
        .songname
          font-size: 12px
          color: rgb(20,20,20)
          font-weight: 400
        .songauthor
          font-size: 11px
          color: rgb(60,60,60)
          margin-top: 3px
</style>



