<template>
  <div class="songslist" v-show="show">
    <div class="listtop-wrapper">
      <img :src="listimg" class="listimg">
      <textarea class="listtext" readonly="readonly" rows="5">{{listtext}}</textarea>
    </div>
    <ul>
      <li v-for="(song, index) of songlist" class="song" v-if="song.title!=null||song.songname!=null" @click="detail(index)">
        <div class="songleft-wrapper">
          <img v-if="song.pic_small||song.thumb||false" :src="song.pic_small||song.thumb" alt="" class="songleft-img" >
          <p v-else class="songindex">{{index+1}}、</p>
        </div>
        <div class="songtext" :class='song.pic_small||song.thumb||false?"addmarginlft":""'>
          <p class="songname">{{song.title||song.songname}}</p>
          <p class="songauthor">{{song.artistname||song.artist||song.author}}</p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  data () {
    return {
      songlist: {},
      listimg: "",
      listtext: "",
      show: false
    }
  },
  methods: {
    setShow () {
      this.show = !this.show
    },
    logList: function(){

    },
    setData (songlistobjstr) {
      let songlistobj = JSON.parse(songlistobjstr)
      this.songlist = songlistobj.songlist
      this.listimg = songlistobj.listimg
      this.listtext = songlistobj.listtext
    },
    detail: function (songindex) {
      let playsonglistobj = {
        songlist: {},
        index: 0
      }
      playsonglistobj.songlist = this.songlist
      playsonglistobj.index = songindex
      this.$emit('playsonglistobjstr', JSON.stringify(playsonglistobj))
    }
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
.songslist
  position: fixed
  left: 0
  width: 100%
  top: 44px
  bottom: 64px
  z-index: 150
  overflow-y: scroll
  background-color: rgb(240, 240, 240)
  .listtop-wrapper
    width: 100%
    overflow: hidden
    background: url('./textbg.png')
    display: flex
    align-items: center
    font-size: 0
    .listimg
      width: 50%
    .listtext
      width: 50%
      font-size: 14px
      font-family: 'Hiragino Sans GB', Helvetica, 'Microsoft Yahei', '微软雅黑', Arial, sans-serif
      font-weight: 100
      color: white
      border: none
      background-color: rgba(240,240,240, 0)
      outline:none
      resize:none
      padding-left: 10px
      line-height: 20px
  .song
    color: black
    height: 53px
    margin-top: 2px
    padding-left: 2px
    background-color: rgb(250,250,250)
    list-style: none
    overflow: hidden
    display: flex
    align-items: center
    font-size: 0
    .songleft-wrapper
      height: 100%
      width: 50px
      float: left
      vertical-align: middle
      .songleft-img
        height: 100%
      .songindex
        font-style: italic
        font-size: 15px
        width: 100%
        height: 100%
        text-align: center
        line-height: 53px
        align-items: center
    .songtext
      float: left
      vertical-align: middle
      &.addmarginlft
        margin-left: 15px
      .songname
        font-size: 14px
        color: black
      .songauthor
        font-size: 11px
        color: rgb(60,60,60)
        margin-top: 3px
</style>



