<template>
<div>
  <div class="myheader">
    <a><i @click="back" class="back fa fa-angle-double-left"></i></a>
    <h1><i class="fa fa-music"></i> Music Player</h1>
    <a><i @click="search" class="search fa fa-search"></i></a>
  </div>
  <ul class="mainbar">
    <li v-for="(name, index) of names" @click="handler(index,getPath(index))" class="item" :class='select===index?"on":""'>{{name}}</li>
  </ul>
</div>
</template>

<script type="text/ecmascript-6">
import routerPath from '../../router/index.js'
export default{
  data () {
    return {
      names: ["新歌速递", "电台列表", "歌手列表", "榜单"],
      select: 0
    }
  },
  created () {
    for (let i = 0; i < routerPath.options.routes.length; i++) {
      if (routerPath.options.routes[i].path === this.$route.path) {
        this.select = i
        return
      }
    }
  },
  methods: {
    handler(num,path){
      this.select = num;
      this.$router.push(routerPath.options.routes[num].path);
    },
    getPath(num){
      return 0;
    },
    back () {
      this.$emit('back')
    },
    search () {
      this.$emit('search')
    }
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
.myheader
  position: relative
  height: 45px
  width: 100%
  background-color: rgb(50, 10, 10)
  font-size: 0
  z-index: 200
  .back, .search
    display: inline-block
    width: 12%
    height: 45px
    line-height: 45px
    text-align: center
    font-size: 18px
    color: #fff
  h1
    display: inline-block
    width: 76%
    height: 45px
    line-height: 45px
    box-sizing: border-box
    text-align: center
    font-size: 16px
    font-weight: 400
    color: #fff
.mainbar
  position: absolute
  top: 45px
  width: 100%
  height: 25px
  font-size: 0
  background-color: #fff
  z-index: 150
  .item
    display: inline-block
    font-size: 13px
    width: 25%
    color: rgb(50,50,50)
    height: 25px
    line-height: 25px
    text-align: center
    box-sizing: border-box
    &.on
      border-bottom: 2px solid rgb(50,10,10);
</style>



