import Vue from 'vue'
import Router from 'vue-router'
import NewSong from '../components/newsong/newsong.vue'
import RadioStation from '../components/radiostation/radiostation'
import Singer from '../components/singer/singer'
import List from '../components/list/list'

Vue.use(Router)

export default new Router({
  routes: [
    {path: '/newsongs', component: NewSong},
    {path: '/radiostation', component: RadioStation},
    {path: '/singer', component: Singer},
    {path: '/list', component: List},
    {path: '/', redirect: 'newsongs'}
  ]
})



