<template>
  <div class="radiostation" ref="radiostation">
    <div>
      <div class="radioimg-wrapper">
        <img :src="imgsrc" alt="" class="radioimg">
      </div>
      <div v-for="radiotype of radiotypes.result" class="radiotype-wrapper">
        <p class="radiotype-name"><span>o</span>{{radiotype.title}}</p>
        <ul class="radios-wrapper">
          <li v-for="(radio, index) of radiotype.channellist" class="radio" @click="detail(index, radiotype)">
            <img :src="radio.thumb||radio.avatar" alt="" class="radio-img">
            <p class="radio-name">{{radio.name}}</p>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  data () {
    return{
      radiotypes: [],
    }
  },
  computed: {
    imgsrc () {
      return require('./电台.jpg')
    }
  },
  created () {
    this.createRadioJson()
  },
  methods: {
    createRadioJson () {
      let jsonp = require('jsonp')
      let vm = this
      jsonp("http://tingapi.ting.baidu.com/v1/restserver/ting?from=qianqian&version=2.1.0&method=baidu.ting.radio.getCategoryList&format=json", null, function(err,data){
        if (err) {
          console.error(err.message)
        } else {

          vm.radiotypes = data
        }
      })
    },
    detail (index, radiotype) {
      let songlistobj = {
        songlist: {},
        listtext: "",
        listimg: ""
      }
      this.$emit('songlistobjstr', JSON.stringify(songlistobj))
      this.$emit('songlistshow')
      let jsonp = require('jsonp')
      let vm = this
      let name = radiotype.channellist[index].name;
      if (radiotype.title === "音乐人频道") {
        jsonp("http://tingapi.ting.baidu.com/v1/restserver/ting?from=qianqian&version=2.1.0&method=baidu.ting.search.catalogSug&format=json&query="+name, null, function (err, data) {
          if (err) {
            console.log(err)
          } else {
            songlistobj.songlist = data.song
            jsonp("http://tingapi.ting.baidu.com/v1/restserver/ting?from=qianqian&version=2.1.0&method=baidu.ting.artist.getinfo&format=json&tinguid="+data.artist[0].artistid, null, function (err, data) {
              if (err) {
                console.log(err)
              } else {
                var contrubute = "姓名："+ name + "\n地区：" + data.country + "\n体重：" + data.weight + "kg\n身高：" + data.stature + "cm\n星座：" + data.constellation
                songlistobj.listtext = contrubute
                songlistobj.listimg = data.avatar_s500
                let songlistobjstr = JSON.stringify(songlistobj)
                vm.$emit('songlistobjstr', songlistobjstr)
              }
            })
          }
        })
      } else {
        let ch_name = radiotype.channellist[index].ch_name;
        jsonp("http://tingapi.ting.baidu.com/v1/restserver/ting?from=qianqian&version=2.1.0&method=baidu.ting.radio.getChannelSong&format=json&pn=0&rn=20&channelname="+ch_name, null, function (err, data) {
          if (err) {
            console.log(err)
          } else {
            songlistobj.songlist = data.result.songlist
            songlistobj.listtext = radiotype.title+"——"+name
            songlistobj.listimg = radiotype.channellist[index].thumb
            let songlistobjstr = JSON.stringify(songlistobj)
            vm.$emit('songlistobjstr', songlistobjstr)
          }
        })
      }
    }
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
.radiostation
  position: fixed
  top: 70px
  left: 0
  bottom: 64px
  width: 100%
  overflow-y: scroll
  .radioimg-wrapper
    width: 100%
    font-size: 0
    background-color: #fff
    text-align: center
    .radioimg
      width: 100%
      max-width: 500px
  .radiotype-wrapper
    background-color: white
    border-bottom: 2px solid rgb(100,10,10)
    font-size: 0
    &:last-child
      border: none
    .radiotype-name
      color: rgb(50,10,10)
      padding: 6px 20px 8.5px 20px
      font-size: 15px
      span
        font-size: 20px
        margin-right: 5px
        width: 100%
    .radios-wrapper
      overflow: hidden
      background-color: rgb(250,250,250)
      .radio
        width: 33.33%
        float: left
        border: 1px solid rgb(250,250,250)
        background-color: white
        box-sizing:border-box
        list-style: none
        .radio-img
          width: 100%
        .radio-name
          color: rgb(70,70,70)
          font-size: 12px
          text-align: center
          padding: 4px
</style>



