function getlyric(lyric) {
  //如果lyric的内容是"[xx:xx.xx]歌词1[yy:yy.yy]歌词二"
  //那str这个数组长度是3
  //str[0]=""
  //str[1]="xx:xx.xx]歌词一"
  //str[2]="yy:yy.yy]歌词二"
  var str = lyric.split("[");
  //因为str[0]="",所以跳过它
  var shijianshuzu = [];
  var gecishuzu = [];
  for (var i = 1; i < str.length; i++) {
    //str[i]格式是00:11.22]我
    //shijian格式是00:11.22
    var shijian = str[i].split(']')[0];
    //geci格式是"我"
    var geci = str[i].split(']')[1];
    var fen = shijian.split(":")[0];

    var miao = shijian.split(".")[0].split(":")[1];
    //xx:xx.xx 时间转换成总的秒数
    var sec = parseInt(fen) * 60 + parseInt(miao);

    //存时间
    shijianshuzu[i - 1] = sec + 6;

    //存歌词
    gecishuzu[i - 1] = geci;
  }
  var obj = {
    time: shijianshuzu,
    lyric: gecishuzu
  };
  return obj;
}

