<template>
  <div class="list" ref="list">
    <div>
      <div v-for="rankingtype of rankingtypes" class="ranking-wrapper"  @click='detail(rankingtype)'>
        <div class="rankingimg-wrapper">
          <img :src="imgsrc(rankingtype.billboard.pic_s260)" alt="" class="rankingimg">
        </div>
        <div class="ranking-text">
          <p class="ranking-comment">{{rankingtype.billboard.comment}}</p>
          <div class="ranking-songs">
            <p v-for="n in 3" class="ranking-song">{{n}}、{{rankingtype.song_list[n-1].title}}——{{rankingtype.song_list[n-1].artist_name}}</p>
            <p class="ranking-song">……</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  data(){
    return{
      rankingjson: [
        'http://tingapi.ting.baidu.com/v1/restserver/ting?from=qianqian&version=2.1.0&method=baidu.ting.billboard.billList&format=json&type=1&offset=0&size=50',
        'http://tingapi.ting.baidu.com/v1/restserver/ting?from=qianqian&version=2.1.0&method=baidu.ting.billboard.billList&format=json&type=2&offset=0&size=50',
        'http://tingapi.ting.baidu.com/v1/restserver/ting?from=qianqian&version=2.1.0&method=baidu.ting.billboard.billList&format=json&type=8&offset=0&size=50）Hito中文榜',
        'http://tingapi.ting.baidu.com/v1/restserver/ting?from=qianqian&version=2.1.0&method=baidu.ting.billboard.billList&format=json&type=6&offset=0&size=50',
        'http://tingapi.ting.baidu.com/v1/restserver/ting?from=qianqian&version=2.1.0&method=baidu.ting.billboard.billList&format=json&type=7&offset=0&size=50',
        'http://tingapi.ting.baidu.com/v1/restserver/ting?from=qianqian&version=2.1.0&method=baidu.ting.billboard.billList&format=json&type=11&offset=0&size=50',
        'http://tingapi.ting.baidu.com/v1/restserver/ting?from=qianqian&version=2.1.0&method=baidu.ting.billboard.billList&format=json&type=14&offset=0&size=50',
        'http://tingapi.ting.baidu.com/v1/restserver/ting?from=qianqian&version=2.1.0&method=baidu.ting.billboard.billList&format=json&type=21&offset=0&size=50',
        'http://tingapi.ting.baidu.com/v1/restserver/ting?from=qianqian&version=2.1.0&method=baidu.ting.billboard.billList&format=json&type=22&offset=0&size=50',
        'http://tingapi.ting.baidu.com/v1/restserver/ting?from=qianqian&version=2.1.0&method=baidu.ting.billboard.billList&format=json&type=23&offset=0&size=50',
        'http://tingapi.ting.baidu.com/v1/restserver/ting?from=qianqian&version=2.1.0&method=baidu.ting.billboard.billList&format=json&type=25&offset=0&size=50',
      ],
      rankingtypes: [],
    }
  },
  computed: {

  },
  mounted: function(){
    this.createRanking()
  },
  methods: {
    imgsrc (url) {
      if (url==="http://a.hiphotos.baidu.com/ting/pic/item/8c1001e93901213fa58c25da56e736d12f2e957c.jpg") {
        return require('./ktv.jpg')
      } else if (url === "http://c.hiphotos.baidu.com/ting/pic/item/6a63f6246b600c336e17b5ce184c510fd9f9a14e.jpg") {
        return require('./billboard.jpg')
      } else if (url === "http://a.hiphotos.baidu.com/ting/pic/item/4afbfbedab64034fed53a900adc379310a551d1d.jpg") {
        return require('./ciza.jpg')
      } else if (url === "http://b.hiphotos.baidu.com/ting/pic/item/9f2f070828381f3052bae5afab014c086e06f011.jpg") {
        return require('./movie.jpg')
      } else {
        return url
      }
    },
    createRanking: function(){
      let jsonp = require('jsonp')
      let vm = this
      for (var i = 0; i < vm.rankingjson.length; i++) {
        jsonp(vm.rankingjson[i], null, function (err, data) {
          if (err) {
            console.log(err)
          } else {
            vm.rankingtypes.push(data)
          }
        })
      }
    },
    detail: function (rankingtype) {
      let songlistobj = {
        songlistobj: {},
        listtext: "",
        listimg: ""
      }
      songlistobj.songlist = rankingtype.song_list
      songlistobj.listtext = "——"+rankingtype.billboard.comment
      songlistobj.listimg = this.imgsrc(rankingtype.billboard.pic_s260)
      this.$emit('songlistobjstr', JSON.stringify(songlistobj))
      this.$emit('songlistshow')
    }
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
.list
  position: fixed
  top: 70px
  left: 0
  bottom: 64px
  width: 100%
  overflow-y: scroll
  .ranking-wrapper
    width: 100%
    background-color: white
    overflow: hidden
    display: flex
    align-items: center
    position: relative
    margin-bottom: 1px
    .rankingimg-wrapper
      width: 42.86%
      float: left
      .rankingimg
        width: 100%
        float: left
    .ranking-text
      width: 57.14%
      height: 100%
      float: left
      color: rgb(40,40,40)
      font-size: 13px
      .ranking-comment
        position: absolute
        top: 0
        font-size: 14px
        display: -webkit-box
        -webkit-box-orient: vertical
        -webkit-line-clamp: 2
        overflow: hidden
        font-family: bold
      .ranking-songs
        transform: translate(0, 19px)
        .ranking-song
          overflow: hidden
          text-overflow: ellipsis
          white-space: nowrap
          line-height:25px
          padding-left: 5px
</style>



