<template>
  <div class="newsong" ref="newsong">
    <div>
      <div class="img-wrapper">
        <img class="mainimg" src="http://p4.music.126.net/SKcbLGcdzj-EE4a1dv2wwA==/18509178743923253.jpg" alt="">
      </div>
      <ul>
        <li v-for="(song, index) of songs.song_list" class="newsonglist" @click="detial(index)">
          <div class="songimg-wrapper">
            <img height="100%" :src="song.avatar_middle" alt="" class="newsonglist-img">
          </div>
          <div class="text-wrapper">
            <p class="songname">{{song.title}}</p>
            <p class="songauthor">{{song.author}}</p>
          </div>
        </li>
      </ul>
    </div>

  </div>
</template>

<script type="text/ecmascript-6">
export default {
  data () {
    return {
      songs: {}
    }
  },
  created () {
    this.cartView()

  },
  methods: {
    detial (index) {
      let playsonglistobj = {
        songlist: {},
        index: 0
      }
      playsonglistobj.songlist = this.songs
      playsonglistobj.index = index
      this.$emit('playsonglistobjstr', JSON.stringify(playsonglistobj))
    },
    cartView: function () {
      let jsonp = require('jsonp')
      let vm = this
      jsonp("http://tingapi.ting.baidu.com/v1/restserver/ting?from=qianqian&version=2.1.0&method=baidu.ting.plaza.getNewSongs&format=json&limit=50", null, function(err,data){
        if (err) {
          console.error(err.message);
        } else {
          vm.songs = data;
        }
      })
    },
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
.newsong
  position: fixed
  top: 70px
  left: 0
  bottom: 64px
  width: 100%
  overflow-y: scroll
  .img-wrapper
    text-align: center
    width: 100%
    font-size: 0
    background-color: #fff
    overflow: hidden
    .mainimg
      width: 100%
      max-width: 500px
  .newsonglist
    color: black
    height: 53px
    margin-top: 2px
    padding-left: 2px
    background-color: rgb(250,250,250)
    list-style: none
    overflow: hidden
    display: flex
    align-items: center
    .songimg-wrapper
      height: 100%
      width: 50px
      float: left
      vertical-align: middle
    .text-wrapper
      float: left
      vertical-align: middle
      margin-left: 10px
      .songname
        font-size: 12px
        color: rgb(20,20,20)
        font-weight: 400
      .songauthor
        font-size: 11px
        color: rgb(60,60,60)
        margin-top: 3px
</style>



