<template>
  <div class="player" @click="showPlaysong">
    <input ref="progress" type="range" :value="progressindex" id="progess" @mousedown="down" @change='speed'>
    <div class="img-wrapper playimg">
      <img :src="img" alt="">
    </div>
    <div class="text">
      <p class="name">{{name}}</p>
      <p class="author">{{author}}</p>
    </div>
    <div class="controls">
      <button class="active" @click.stop='last()'><i class="fa fa-backward"></i></button>
      <button class="active" @click.stop='play()'><i class="fa" :class="isPlay===false?'fa-play':'fa-pause'"></i></button>
      <button class="active" @click.stop='next()'><i class="fa fa-forward"></i></button>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import axios from 'axios'
export default {
  data () {
    return {
      name: "",
      author: "",
      img: require('./123.jpg'),
      isPause: false,
      songsrc: require('./生来彷徨.mp3'),
      isPlay: false,
      audio: new Audio(this.songsrc),
      progressindex: 0,
      time: {}
    }
  },
  created () {
    this.audio.src = this.songsrc
  },
  methods: {
    last () {
      this.$emit('last')
    },
    startTimer () {
      clearInterval(this.time)
      this.time = setInterval(() => {
        this.progressindex += 100 / (this.audio.duration - 0)
      }, 1000)
    },
    stopTimer () {
      clearInterval(this.time)
    },
    setData (obj) {
      this.progressindex = 0
      this.isPlay = false
      this.audio.load()
      this.name = obj.name
      this.author = obj.author
      this.img = obj.img
      this.audio.src = obj.songsrc
    },
    play () {
      if (this.songsrc!=""){
        if (this.audio.paused) {
          this.audio.play()
          this.isPlay = true
          this.startTimer()
        } else {
          this.audio.pause()
          this.isPlay = false
          this.stopTimer()
        }
        this.$emit("startLyric")
      }
    },
    next () {
      this.$emit('next')
    },
    showPlaysong () {
      this.$emit('showPlaysong')
    },
    down () {
      console.log("单击")
    },
    speed () {
      this.progressindex = this.$refs.progress.value - 0
      this.audio.currentTime = this.progressindex * this.audio.duration / 100
      this.$emit("setTimeindex")
    }
  },
  watch: {
    'progressindex' () {
      if (this.progressindex >= 100) {
        this.stopTimer()
        this.next()
      }
    }
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
.player
  position: relative
  display: flex
  width: 100%
  max-width: 600px
  height: 64px
  align-items: center
  z-index: 200
  .img-wrapper
    flex: 0 0 64px
    padding-left: 4px
    width: 60px
    height: 60px
    margin-top: 3px;
    align-items: center
    box-sizing: border-box
    img
      width: 60px
      border-radius: 50%
      float: left
      border: 1px solid rgba(50, 10, 10, 0.5)
      padding: 2px
      box-sizing: border-box
  .text
    flex: 1
    padding-left: 3px
    .name
      font-size: 14px;
      color: black;
    .author
      font-size: 12px;
      color: rgb(60,60,60);
      margin-top: 3px;
  .controls
    flex: 0 0 138px
    width: 138px
    text-align: center
    font-size: 0
    margin-right: 2px
    button
      width: 38px
      height: 38px
      background: white
      border-radius: 50%
      margin-right: 12px
      &:focus
        outline: none
      &:last-child
        margin-right: 0
  input
    position: absolute
    top: 0
    left: 0
    width: 100%
    margin: 0
    &[type=range]:focus
      outline: 0
    &[type=range]
      -webkit-appearance: none
      width: 100%
      position: absolute
      top: 0
    &[type=range]::-webkit-slider-runnable-track
      width: 100%
      height: 3px
      box-sizing: none
      cursor: pointer
      background: #19202D
      border-radius: 3px
      z-index:200
      position:relative
    &[type=range]::-webkit-slider-thumb
      -webkit-appearance: none
      height: 10px
      width: 10px
      border-radius: 50%
      background-color: rgb(50,10,10)
      cursor: pointer
      margin-top: -3.5px
      overflow: hidden
</style>



