<template>
  <div class="songer" ref="songer">
    <div>
      <ul class="singers-wrapper">
        <li class="singer" v-for="singer of singers.artist" @click="detail(singer.ting_uid, singer.avatar_big)">
          <img :src="singer.avatar_middle" alt="" class="singer-img">
          <p class="singer-name">{{singer.name}}</p>
        </li>
      </ul>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  data(){
    return{
      singers: []
    }
  },
  mounted: function(){
    this.creatSingerList()
  },
  methods: {
    creatSingerList () {
      let jsonp = require('jsonp')
      let vm = this
      jsonp("http://tingapi.ting.baidu.com/v1/restserver/ting?from=qianqian&version=2.1.0&method=baidu.ting.artist.get72HotArtist&format=json&order=1&offset=0&limit=50", null, function(err, data) {
        if (err){
          console.log(err)
        } else {
          vm.singers = data
        }
      })
    },
    detail: function (id, img) {
      if (id===null) {
        alert("对不起，缺少该页面的歌曲数据，无法显示");
      }else{
        let songlistobj = {
          songlist: {},
          listtext: "",
          listimg: ""
        }
        this.$emit('songlistobjstr', JSON.stringify(songlistobj))
        this.$emit('songlistshow')
        let jsonp = require('jsonp')
        let vm = this
        jsonp("http://tingapi.ting.baidu.com/v1/restserver/ting?from=qianqian&version=2.1.0&method=baidu.ting.artist.getSongList&format=json&order=2&tinguid="+id+"&offset=0&limits=50", null, function(err, data){
          if (err) {
            console.log(err)
          } else {
            songlistobj.songlist = data.songlist;
            jsonp("http://tingapi.ting.baidu.com/v1/restserver/ting?from=qianqian&version=2.1.0&method=baidu.ting.artist.getinfo&format=json&tinguid="+id, null,function(err, data){
              if (err) {
                console.log(err)
              } else {
                let contrubute = "姓名："+ data.name + "\n地区：" + data.country + "\n体重：" + data.weight + "kg\n身高：" + data.stature + "cm\n星座：" + data.constellation;
                songlistobj.listtext = contrubute
                songlistobj.listimg = img
                let songlistobjstr = JSON.stringify(songlistobj)
                vm.$emit('songlistobjstr', songlistobjstr)
              }
            })
          }
        })
      }
    }
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
.songer
  position: fixed
  top: 70px
  left: 0
  bottom: 64px
  width: 100%
  overflow-y: scroll
  .singers-wrapper
    overflow: hidden
    background-color: rgb(250,250,250)
    .singer
      width: 33.33%
      float: left
      border: 1px solid rgb(250,250,250)
      background-color: white
      box-sizing:border-box
      list-style: none
      font-size: 0
      .singer-img
        width: 100%
      .singer-name
        color: rgb(70,70,70)
        font-size: 12px
        text-align: center
        padding: 4px
</style>



